import { createWebHistory, createRouter } from 'vue-router'

import Menu from '../components/Pages/MenuPage.vue'
import Exam from '../components/Pages/ExamPage.vue'
import MarathonPage from '../components/Pages/MarathonPage.vue'
import StatisticPage from '../components/Pages/StatisticPage.vue'
import MistakePage from '../components/Pages/MistakePage.vue'
import ThemePage from '../components/Pages/ThemePage.vue'
import ThemeTraining from '../components/Pages/ThemeTraining.vue'
import PrintPage from '../components/Pages/PrintPage.vue'

const routes = [
	{ path: '/', component: Menu },
	{ path: '/exam', component: Exam },
	{ path: '/marathon', component: MarathonPage },
	{ path: '/statistic', component: StatisticPage },
	{ path: '/mistake', component: MistakePage },
	{ path: '/theme', component: ThemePage },
	{ path: '/theme/:theme', component: ThemeTraining },
	{ path: '/print', component: PrintPage },
]

const router = createRouter({
	history: createWebHistory('/eb_4'),
	routes,
})

export default router
