<script setup>
import ButtonMenu from '../UI/ButtonMenu.vue'

const props = defineProps({
	theme: {
		type: Object,
		required: true,
	},
})

const emit = defineEmits(['chooseTheme'])

const handleClick = () => {
	emit('chooseTheme', props.theme.name)
}
</script>

<template>
	<div class="container">
		<div class="theme_name">
			{{ theme.name }}
		</div>

		<div class="theme_counts">
			<hr />
			<div class="theme_total">
				Количество вопросов: {{ theme.questionTotal }}
			</div>

			<div class="theme_good">
				Количество правильных ответов : {{ theme.questionGood }}
			</div>

			<div class="theme_fail">Количество ошибок: {{ theme.questionFail }}</div>
		</div>

		<div class="theme_btn">
			<ButtonMenu @click="handleClick">Тренироваться</ButtonMenu>
		</div>
	</div>
</template>

