<script setup>
import { store } from '../../store/store.js'
import { ref, onBeforeMount } from 'vue'
import { RouterLink, useRouter } from 'vue-router'
import ThemeCard from '../UI/ThemeCard.vue'
import ButtonMenu from '../UI/ButtonMenu.vue'

const router = useRouter()
const themeArr = ref([])

onBeforeMount(() => {
	const themes = [...new Set(store.data.map(question => question.theme))]

	themes.forEach(el => {
		themeArr.value.push({
			name: el,
			questionTotal: store.data.filter(item => item.theme === el).length,
			questionGood: store.data.filter(
				item =>
					item.theme === el &&
					item.userAlreadyAnswer === true &&
					item.isCorrect === true
			).length,
			questionFail: store.data.filter(
				item =>
					item.theme === el &&
					item.userAlreadyAnswer === true &&
					item.isCorrect === false
			).length,
		})
	})
})

const handleChooseTheme = e => {
	router.push(`/theme/${encodeURIComponent(e)}`)
}
</script>

<template>
	<div class="container">
		<div class="container__card">
			<div
				v-for="(item, index) in themeArr"
				:key="index"
				:class="{
					'last-item':
						index === themeArr.length - 1 && themeArr.length % 2 !== 0,
				}"
			>
				<ThemeCard :theme="item" @chooseTheme="handleChooseTheme"></ThemeCard>
			</div>
		</div>

		<div class="container__btn">
			<RouterLink to="/">
				<ButtonMenu>Вернуться в меню</ButtonMenu>
			</RouterLink>
		</div>
	</div>
</template>

<style scoped>
.container {
	margin: 0.5vw 0.8vw 2vw 0.4vw;
}

.container__card {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: 1.5vw 0.7vw;
}

.last-item {
	margin-top: 1vw;
	grid-column: span 2;
	justify-self: center;
	max-width: 50%;
}

.container__btn {
	margin-top: 3vw;
	display: flex;
	justify-content: center;
}
</style>
