<script setup>
const props = defineProps({
	fontSize: {
		type: String,
		default: '1em', // Значение по умолчанию
	},
	disabled: {
		type: Boolean,
		default: false,
	},
	width: {
		type: String,
		default: '45vw', // Значение по умолчанию
	},
	padding: {
		type: String,
		default: '1vw 4vw',
	},
})
</script>

<template>
	<button
		:style="{
			fontSize: props.fontSize,
			width: props.width,
			padding: props.padding,
		}"
	>
		<slot></slot>
	</button>
</template>

<style scoped>
button {
	font-weight: 500;
	background-color: #234781;
	color: #fafafa;
	border: none;
	cursor: pointer;
	transition: background-color 0.5s;
}

button:hover {
	background-color: #4f6c9a;
}
</style>
