<script setup>
import { resultExam } from '@/store/examData.js'
import { store } from '@/store/store.js'
import html2pdf from 'html2pdf.js'
import ButtonMenu from '../UI/ButtonMenu.vue'
import { RouterLink } from 'vue-router'

const formatDate = (() => {
	const date = new Date(resultExam.startTime)
	const day = String(date.getDate()).padStart(2, '0')
	const month = String(date.getMonth() + 1).padStart(2, '0')
	const year = date.getFullYear()

	return `${day}.${month}.${year}`
})()

function generatePDF() {
	const elementToPrint = document.querySelector('.container')
	const opt = {
		margin: 10,
		filename: `Протокол проверки знаний ${resultExam.lastName}`,
		pagebreak: { mode: ['avoid-all', 'css', 'legacy'] },
	}
	html2pdf().set(opt).from(elementToPrint).save()
}

function print() {
	window.print()
}
</script>

