}

.mb-4 {
	margin-bottom: 4vw;
}

.mt-5 {
	margin-top: 5vw;
}

.row {
	margin: 2vw 0;
	display: grid;
	grid-template-columns: auto 1fr;
}

.border_bt {
	margin-left: 6vw;
	border-bottom: 2px solid black;
	padding: 0 0 0.4vw 0.2em; /* Отступ под линией для текста */
	position: relative; /* Для позиционирования внутреннего содержимого */
	display: flex;
	justify-content: center; /* Центрируем текст под линией */
	align-items: flex-end; /* Текст под линией */
}

.center-decryption {
	position: relative;
	top: 1.5em; /* Смещаем текст под линию */
	font-size: 0.85em; /* Размер шрифта */
	color: black;
}

.fill {
	margin-top: 5vw;
}

.empty-line {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: flex-end;
}

.line {
	margin: 4vw 0;
	width: 100%;
	height: 1vw;
	border-bottom: 1px solid black;
}

@media print {
