<script setup>
import { ref, watch } from 'vue'

//const props = defineProps(['answerArr', 'isRadio', 'isConfirm'])
const props = defineProps({
	answerArr: {
		type: Array,
		required: true,
	},
	isRadio: {
		type: Boolean,
		required: true,
	},
	isConfirm: {
		type: Boolean,
		required: false,
	},
})
const emit = defineEmits(['userSelect'])
const isDisable = ref(false)

// Реактивное состояние для хранения выбранных значений
const selectedValue = ref(null) // Для радио-кнопок
const selectedValues = ref([]) // Для чекбоксов

// Сброс состояния при изменении answerArr
watch(
	() => props.answerArr,
	() => {
		selectedValue.value = null // Сброс для радио-кнопок
		selectedValues.value = [] // Сброс для чекбоксов
		isDisable.value = false
	}
)

watch(
	() => props.isConfirm, // Геттер-функция, возвращающая значение пропса
	newVal => {
		if (newVal === false) return // Если isConfirm false, ничего не делаем
		isDisable.value = true
		if (selectedValue.value !== null) emit('userSelect', selectedValue.value)
		if (selectedValues.value.length > 0)
			emit('userSelect', selectedValues.value)
	}
)

// Обработчик изменения для радио-кнопок
const handleRadioChange = value => {
	selectedValue.value = value
}

// Обработчик изменения для чекбоксов
const handleCheckboxChange = value => {
	if (selectedValues.value.includes(value)) {
		// Удаляем значение, если оно уже выбрано
		selectedValues.value = selectedValues.value.filter(item => item !== value)
	} else {
		// Добавляем значение, если оно не выбрано
		selectedValues.value.push(value)
	}
}
</script>

<template>
	<div class="container">
		<div v-if="isRadio">
			<label v-for="item in answerArr" :key="item" class="form-control">
				<input
					type="radio"
					name="radio"
					:value="item"
					:checked="selectedValue === item"
					:disabled="isDisable"
					@change="handleRadioChange(item)"
				/>
				<span style="white-space: pre-line">{{ item }}</span>
			</label>
		</div>
		<div v-else>
			<label v-for="item in answerArr" :key="item" class="form-control">
				<input
					type="checkbox"
					name="checkbox"
					:value="item"
					:checked="selectedValues.includes(item)"
					:disabled="isDisable"
					@change="handleCheckboxChange(item)"
				/>
				<span style="white-space: pre-line">{{ item }}</span>
			</label>
		</div>
	</div>
</template>

<style scoped>
.form-control {
	margin: 1vw 0;
	display: grid;
	grid-template-columns: 1em auto;
	gap: 0.5em;
	align-items: center;
}

label {
	cursor: pointer;
}

input[type='radio'],
input[type='checkbox'] {
	-webkit-appearance: none;
	appearance: none;
	margin: 0;
	font: inherit;
	color: black;
	width: 2.8vw;
	height: 2.8vw;
	border: 0.4vw solid black;
	border-radius: 50%;
	transform: translateY(-0.075em);
	cursor: pointer;
	display: grid;
	place-content: center;
}

input[type='checkbox'] {
	border-radius: 0.15em;
}

input[type='radio']::before,
input[type='checkbox']::before {
	content: '';
	width: 1.6vw;
	height: 1.6vw;
	transform: scale(0);
	transition: 120ms transform ease-in-out;
	background-color: #234781;
}

input[type='radio']::before {
	border-radius: 50%;
}

input[type='checkbox']::before {
	clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
}

input[type='radio']:checked::before,
input[type='checkbox']:checked::before {
	transform: scale(1);
}

input:not(:checked):not(:disabled) {
	opacity: 1;
}

input:not(:checked):disabled {
	opacity: 0.5;
}

input:checked:not(:disabled) {
	opacity: 1;
}

input:checked:disabled {
	opacity: 1;
	font-weight: bold;
}

label:has(input:disabled) {
	opacity: 0.5;
}

/* Стили для label, когда input выбран (checked) */
label:has(input:checked:disabled) {
	font-weight: bold;
	opacity: 1;
}
input:has(input:checked:disabled) {
	opacity: 1;
}
</style>
