<template>
	<div class="container">
		<div class="container__close">
			<span>Введите ваши данные</span>
			<img @click="$emit('close')" src="@/assets/img/close.svg" alt="" />
		</div>

		<div class="line"></div>

		<div class="container__form">
			<form @submit.prevent="validateForm">
				<div :class="['form-group', { invalid: errors.firstName }]">
					<input
						v-model="formData.firstName"
						type="text"
						id="firstName"
						placeholder=" "
						@blur="onBlur('firstName')"
					/>
					<label for="firstName">Имя</label>
					<div v-if="errors.firstName" class="error">
						{{ errors.firstName }}
					</div>
				</div>
				<div :class="['form-group', { invalid: errors.lastName }]">
					<input
						v-model="formData.lastName"
						type="text"
						id="lastName"
						placeholder=" "
						@blur="onBlur('lastName')"
					/>
					<label for="lastName">Фамилия</label>
					<div v-if="errors.lastName" class="error">
						{{ errors.lastName }}
					</div>
				</div>
				<div :class="['form-group', { invalid: errors.middleName }]">
					<input
						v-model="formData.middleName"
						type="text"
						id="middleName"
						placeholder=" "
						@blur="onBlur('middleName')"
					/>
					<label for="middleName">Отчество</label>
					<div v-if="errors.middleName" class="error">
						{{ errors.middleName }}
					</div>
				</div>
				<div :class="['form-group', { invalid: errors.organization }]">
					<input
						v-model="formData.organization"
						type="text"
						id="organization"
						placeholder=" "
						@blur="onBlur('organization')"
					/>
					<label for="organization">Организация</label>
					<div v-if="errors.organization" class="error">
						Поле не должно быть пустым
					</div>
				</div>
				<div :class="['form-group', { invalid: errors.position }]">
					<input
						v-model="formData.position"
						type="text"
						id="position"
						placeholder=" "
						@blur="onBlur('position')"
					/>
					<label for="position">Должность</label>
					<div v-if="errors.position" class="error">
						{{ errors.position }}
					</div>
				</div>
				<div class="container__btn">
					<Button>Продолжить</Button>
				</div>
			</form>
		</div>
	</div>
</template>

<script setup>
import { useRouter } from 'vue-router'
import { reactive } from 'vue'
import Button from './Button.vue'
import { resultExam } from '@/store/examData.js'

const emit = defineEmits(['close'])

const formData = reactive({
	firstName: '',
	lastName: '',
	middleName: '',
	organization: '',
	position: '',
})

const errors = reactive({
	firstName: '',
	lastName: '',
	middleName: '',
	organization: '',
	position: '',
})

const noDigitsPattern = /^[^\d]*$/

function validateForm() {
	errors.firstName = !formData.firstName.trim()
		? 'Поле не должно быть пустым'
		: !noDigitsPattern.test(formData.firstName)
		? 'Поле не должно содержать цифры'
		: ''
	errors.lastName = !formData.lastName.trim()
		? 'Поле не должно быть пустым'
		: !noDigitsPattern.test(formData.lastName)
		? 'Поле не должно содержать цифры'
		: ''
	errors.middleName = !noDigitsPattern.test(formData.middleName)
		? 'Поле не должно содержать цифры'
		: ''
	errors.organization = !formData.organization.trim()
		? 'Поле не должно быть пустым'
		: ''
	errors.position = !formData.position.trim()
		? 'Поле не должно быть пустым'
		: !noDigitsPattern.test(formData.position)
		? 'Поле не должно содержать цифры'
		: ''

	const formIsValid = !Object.values(errors).some(error => error)

	if (formIsValid) {
		handleClickForm()
	}
}

function onBlur(field) {
	if (field === 'firstName') {
		errors.firstName = !formData.firstName.trim()
			? 'Поле не должно быть пустым'
			: !noDigitsPattern.test(formData.firstName)
			? 'Поле не должно содержать цифры'
			: ''
	} else if (field === 'lastName') {
		errors.lastName = !formData.lastName.trim()
			? 'Поле не должно быть пустым'
			: !noDigitsPattern.test(formData.lastName)
			? 'Поле не должно содержать цифры'
			: ''
	} else if (field === 'middleName') {
		errors.middleName = !noDigitsPattern.test(formData.middleName)
			? 'Поле не должно содержать цифры'
			: ''
	} else if (field === 'organization') {
		errors.organization = !formData.organization.trim()
			? 'Поле не должно быть пустым'
			: ''
	} else if (field === 'position') {
		errors.position = !formData.position.trim()
			? 'Поле не должно быть пустым'
			: !noDigitsPattern.test(formData.position)
			? 'Поле не должно содержать цифры'
			: ''
	}
}

const router = useRouter()

function handleClickForm() {
	resultExam.name = formData.firstName
	resultExam.lastName = formData.lastName
	resultExam.middleName = formData.middleName
	resultExam.organization = formData.organization
	resultExam.position = formData.position
	router.push('/print')
}
</script>

<style scoped>
.container {
	width: 52vw;
	background-color: rgb(245, 245, 245);
	position: fixed;
	z-index: 100;
	border-radius: 2.5vw;
	padding: 2vw;
	box-shadow: 0 0 1vw 1vw rgba(0, 0, 0, 0.173);
}

.container__close {
	display: flex;
	justify-content: space-between;
}

.container__close span {
	font-size: 1em;
}

.container__close img {
	width: 3vw;
	cursor: pointer;
	margin-bottom: 2vw;
}

.line {
	margin-bottom: 2.5vw;
	height: 0.1vw;
	background-color: rgba(0, 0, 0, 1);
	box-shadow: 0 0 1vw 1vw #ffffff;
}

.form-group {
	position: relative;
	margin-bottom: 20px;
}

.form-group input {
	width: 100%;
	padding: 1.6vw;
	border: 1px solid #ccc;
	border-radius: 1vw;
	font-size: 0.8em;
	outline: none;
	transition: all 0.3s;
}

.form-group.invalid input {
	border-color: red;
}

.form-group input:focus {
	border-color: #4caf50;
}

.form-group label {
	position: absolute;
	top: 1.5vw;
	left: 1.7vw;
	font-size: 0.8em;
	color: #aaa;
	transition: all 0.3s;
	pointer-events: none;
}

.form-group.invalid label {
	color: red;
}

.form-group input:focus + label,
.form-group input:not(:placeholder-shown) + label {
	top: -2.4vw;
	left: 1.4vw;
	font-size: 0.7em;
	color: #4caf50;
}

.form-group.invalid input:focus + label,
.form-group.invalid input:not(:placeholder-shown) + label {
	color: red;
}

.error {
	font-size: 0.75em;
	color: red;
	margin-top: 0.5vw;
	text-align: center;
}

.container__btn {
	display: flex;
	justify-content: flex-end;
}
</style>
