<script setup>
import { ref } from 'vue'

const props = defineProps({
	backgroundColor: {
		type: String,
		default: '#42b983', // Значение по умолчанию
	},
	fontSize: {
		type: String,
		default: '1em', // Значение по умолчанию
	},
	hoverBackgroundColor: {
		type: String,
		default: '#369f6e', // Значение по умолчанию
	},
	disabled: {
		type: Boolean,
		default: false,
	},
})

const isHovered = ref(false)

const handleMouseEnter = () => {
	if (!props.disabled) {
		isHovered.value = true
	}
}

const handleMouseLeave = () => {
	if (!props.disabled) {
		isHovered.value = false
	}
}
</script>

<template>
	<button
		:style="{
			backgroundColor: isHovered
				? props.hoverBackgroundColor
				: props.backgroundColor,
			fontSize: props.fontSize,
		}"
		@mouseenter="handleMouseEnter"
		@mouseleave="handleMouseLeave"
		:disabled="props.disabled"
	>
		<slot></slot>
	</button>
</template>

<style scoped>
button {
	padding: 1vw 3vw;
	color: white;
	border: none;
	border-radius: 1vw;
	cursor: pointer;
	transition: background-color 0.3s ease;
}

button:disabled {
	background-color: #ccc;
	cursor: not-allowed;
}
</style>
