import { reactive, watch } from 'vue'
import { IndexedDB } from './indexedDB.js'

// Функция для исключения полей isCorrect и userAlreadyAnswer
function excludeFields(item) {
	const { isCorrect, userAlreadyAnswer, ...rest } = item
	return rest
}

const db = new IndexedDB('eb_4', 'questions')

// Функция для работы с localStorage
function getFromLocalStorage(key, defaultValue) {
	const value = localStorage.getItem(key)
	return value !== null ? JSON.parse(value) : defaultValue
}

function setToLocalStorage(key, value) {
	localStorage.setItem(key, JSON.stringify(value))
}

export const store = reactive({
	data: [],
	_isHightVoltage: getFromLocalStorage('isHightVoltage_4', false),
	// Геттер и сеттер для isHightVoltage
	get isHightVoltage() {
		return this._isHightVoltage
	},
	set isHightVoltage(value) {
		this._isHightVoltage = value
		setToLocalStorage('isHightVoltage_4', value) // Сохраняем в localStorage
	},
	async loadData() {
		const startTime = Date.now()
		try {
			// Открываем базу данных
			await db.open()

			// Загружаем данные из data.json
			const response = await fetch('./data.json')
			if (!response.ok) {
				throw new Error('Network response was not ok')
			}
			const jsonData = await response.json()
			let newData = jsonData.data

			// Фильтруем данные в зависимости от isHightVoltage
			if (!this.isHightVoltage) {
				// Если isHightVoltage === false, удаляем вопросы с voltage
				const existingData = await db.getAll()
				const itemsToRemove = existingData.filter(item => item.voltage)
				if (itemsToRemove.length > 0) {
					await db.delete(itemsToRemove.map(item => item.id))
				}

				// Оставляем только вопросы без voltage
				newData = newData.filter(item => !item.voltage)
			} else {
				// Если isHightVoltage === true, добавляем вопросы с voltage
				const existingData = await db.getAll()
				const itemsToAdd = newData.filter(
					item =>
						item.voltage &&
						!existingData.some(existingItem => existingItem.id === item.id)
				)
				if (itemsToAdd.length > 0) {
					// Добавляем поля isCorrect и userAlreadyAnswer
					const enrichedItems = itemsToAdd.map(item => ({
						...item,
						isCorrect: false,
						userAlreadyAnswer: false,
					}))
					await db.save(enrichedItems)
				}
			}

			// Добавляем поля isCorrect и userAlreadyAnswer для новых данных
			const enrichedData = newData.map(item => ({
				...item,
				isCorrect: false,
				userAlreadyAnswer: false,
			}))

			// Получаем текущие данные из IndexedDB
			const existingData = await db.getAll()

			// Сравниваем данные без учета isCorrect и userAlreadyAnswer
			const updates = []
			enrichedData.forEach(newItem => {
				const existingItem = existingData.find(item => item.id === newItem.id)

				// Сравниваем объекты без полей isCorrect и userAlreadyAnswer
				if (
					!existingItem ||
					JSON.stringify(excludeFields(existingItem)) !==
						JSON.stringify(excludeFields(newItem))
				) {
					updates.push(newItem)
				}
			})
			const enrichedDataIds = new Set(enrichedData.map(item => item.id))
			const allExistingItems = await db.getAll()
			const idsToDelete = allExistingItems
				.filter(item => !enrichedDataIds.has(item.id))
				.map(item => item.id)
			if (idsToDelete.length > 0) {
				await db.delete(idsToDelete)
			}

			// Сохраняем обновленные данные в IndexedDB
			if (updates.length > 0) {
				await db.save(updates)
			}

			// Обновляем данные в хранилище
			this.data = await db.getAll()
		} catch (error) {
			console.error('Failed to load or update data:', error)
		}
		const endTime = Date.now()
		const executionTime = endTime - startTime
	},

	// Метод для получения вопроса по id
	async getQuestionById(id) {
		try {
			await db.open()
			const question = await db.getById(id)
			return question
		} catch (error) {
			console.error('Failed to get question by id:', error)
			return null
		}
	},

	// Метод для обновления вопроса
	async updateQuestion(id, updates) {
		try {
			await db.open()
			const question = await db.getById(id)
			if (question) {
				// Преобразуем реактивный объект в простой
				const rawUpdates = JSON.parse(JSON.stringify(updates))
				const rawQuestion = JSON.parse(JSON.stringify(question))
				const updatedQuestion = { ...rawQuestion, ...rawUpdates }

				// Сохраняем обновленный вопрос
				await db.save([updatedQuestion])
			} else {
				console.error('Question not found')
			}
		} catch (error) {
			console.error('Failed to update question:', error)
		}
	},
	async reloadAndReplaceData() {
		try {
			await db.open()

			// Запрос новых данных
			const response = await fetch('./data.json')
			if (!response.ok) {
				throw new Error('Network response was not ok')
			}
			const jsonData = await response.json()
			let newData = jsonData.data

			// Учитываем значение isHightVoltage
			if (!this.isHightVoltage) {
				newData = newData.filter(item => !item.voltage)
			}

			// Добавляем необходимые поля
			const enrichedData = newData.map(item => ({
				...item,
				isCorrect: false,
				userAlreadyAnswer: false,
			}))

			// Полностью перезаписываем базу
			await db.clear()
			await db.save(enrichedData)

			// Обновляем данные в хранилище
			this.data = enrichedData
		} catch (error) {
			console.error('Ошибка при перезаписи данных:', error)
		}
	},
})

// Отслеживаем изменения isHightVoltage
watch(
	() => store.isHightVoltage,
	async newValue => {
		await store.loadData() // Перезагружаем данные при изменении isHightVoltage
	},
	{ immediate: true } // Выполняем сразу при монтировании
)
