<script setup>
import { computed } from 'vue'

const props = defineProps({
	index: {
		type: Number,
		required: true,
	},
	total: {
		type: Number,
		required: true,
	},
	isShowTotal: {
		type: Boolean,
		default: false,
	},
})

const progressWidth = computed(() => {
	return `${((props.index + 1) / props.total) * 100}%`
})
</script>

<template>
	<div class="progress-bar">
		<div class="progress-bar__fill" :style="{ width: progressWidth }"></div>
		<div v-if="isShowTotal" class="progress-bar__text">
			{{ index + 1 }} / {{ total }}
		</div>
	</div>
</template>

<style scoped>
.progress-bar {
	width: 100%;
	height: 3vw;
	background-color: #e0e0e0;
	border-radius: 10px;
	overflow: hidden;
	position: relative;
}

.progress-bar__fill {
	height: 100%;
	background-color: #42b983; /* Цвет заполнения */
	border-radius: 10px;
	transition: width 0.3s ease; /* Плавное изменение ширины */
}

.progress-bar__text {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	font-size: 0.9em;
	color: #333;
	font-weight: bold;
}
</style>
