<script setup>
import { store } from '@/store/store.js'
import { ref, onBeforeMount } from 'vue'
import ButtonMenu from '../UI/ButtonMenu.vue'
import ConfirmDialog from '../UI/ConfirmDialog.vue'

const userAnswerCount = ref(0)
const totalQuestion = ref(0)
const failCount = ref(0)
const goodCount = ref(0)
const theme = ref('')

function calculateStats() {
	const data = store.data
	totalQuestion.value = data.length
	userAnswerCount.value = data.filter(
		item => item.userAlreadyAnswer === true
	).length
	failCount.value = data.filter(
		item => item.userAlreadyAnswer === true && item.isCorrect === false
	).length
	goodCount.value = data.filter(
		item => item.userAlreadyAnswer === true && item.isCorrect === true
	).length

	// Группируем данные по темам и подсчитываем ошибки
	const themeStats = data.reduce((acc, item) => {
		if (item.userAlreadyAnswer) {
			if (!acc[item.theme]) {
				acc[item.theme] = { total: 0, fail: 0 }
			}
			acc[item.theme].total++
			if (!item.isCorrect) {
				acc[item.theme].fail++
			}
		}
		return acc
	}, {})

	// Находим тему с наибольшим процентом ошибок
	let weakestTheme = ''
	let maxFailRate = 0
	for (const [key, value] of Object.entries(themeStats)) {
		const failRate = value.fail / value.total
		if (failRate > maxFailRate) {
			maxFailRate = failRate
			weakestTheme = key
		}
	}

	theme.value = weakestTheme
}

onBeforeMount(() => {
	calculateStats()
})

const dialogVisible = ref(false)

function showConfirmDialog() {
	dialogVisible.value = true
}

async function onConfirm() {
	dialogVisible.value = false
	await store.reloadAndReplaceData()
	calculateStats() // Пересчет статистики после обновления данных
}

function onCancel() {
	dialogVisible.value = false
}
</script>

<template>
	<div class="container">
		<div class="container__head">
			<h2>Статистика</h2>
		</div>

		<div class="container__info">
			Вы ответили на: {{ userAnswerCount }} из {{ totalQuestion }} вопросов
		</div>
		<div class="container__info good">Правильных ответов: {{ goodCount }}</div>
		<div class="container__info fail">
			Неправильный ответов: {{ failCount }}
		</div>
		<div class="container__info theme">
			Ваша самая слабая тема: <br />
			{{ theme }}
		</div>
		<div class="container__btns">
			<RouterLink to="/">
				<ButtonMenu width="30vw"> Вернуться в меню </ButtonMenu>
			</RouterLink>
			<ButtonMenu @click="dialogVisible = true" width="27vw" font-size="0.8em">
				Сбросить статистику
			</ButtonMenu>
		</div>
	</div>
	<ConfirmDialog
		:isVisible="dialogVisible"
		title="Вы уверены?"
		message="Это приведет к потери прогресса"
		@confirm="onConfirm"
		@cancel="onCancel"
	/>
</template>

<style scoped>
.container {
	background-image: url('@/assets/img/img_2.png');
	background-size: cover;
	min-height: 50vw;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.container__head h2 {
	font-size: 2.6em;
	font-weight: 900;
	margin-bottom: 2vw;
}

.container__info {
	margin: 0.2vw 0;
	font-size: 1.3em;
	font-weight: 900;
}

.good {
	color: #42b983;
}

.fail {
	color: red;
}

.theme {
	color: #00277d;
	text-align: center;
}

.container__btns {
	margin: 1.2vw;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 2vw;
}
</style>
