<script setup>
import { RouterLink } from 'vue-router'
import ButtonMenu from './UI/ButtonMenu.vue'
import DialogForm from './UI/DialogForm.vue'
import { ref } from 'vue'

const props = defineProps({
	isExam: {
		type: Boolean,
		default: false,
	},
	goodCount: {
		type: Number,
		required: true,
	},
	failCount: {
		type: Number,
		required: true,
	},
	isMarathon: {
		type: Boolean,
		default: false,
	},
})

const isShowDialog = ref(false)
</script>

<template>
	<div class="container">
		<Transition mode="out-in">
			<div v-if="isShowDialog" class="container__dialog-form">
				<DialogForm @close="isShowDialog = false" />
			</div>
		</Transition>

		<div class="container__head">
			<h2>Результат</h2>
		</div>

		<div class="container__result">
			<div>
				<div v-if="isExam">
					<h3 v-if="goodCount === 10">Отлично</h3>
					<h3 v-if="goodCount === 9">Хорошо</h3>
					<h3 v-if="goodCount === 8 || goodCount === 7">Удовлетворительно</h3>
					<h3 v-if="goodCount < 6">Неудовлетворительно</h3>
				</div>
			</div>
		</div>

		<div class="container__count">
			<div class="container__count_good">
				<span>Правильных ответов:</span> <span>{{ goodCount }}</span>
			</div>
			<div v-if="!isMarathon" class="container__count_fail">
				<span>Неправильных ответов: </span><span>{{ failCount }}</span>
			</div>
		</div>

		<div class="container__btns">
			<ButtonMenu @click="isShowDialog = true" v-if="isExam"
				>Скачать протокол</ButtonMenu
			>
			<RouterLink to="/">
				<ButtonMenu> Вернуться в меню </ButtonMenu>
			</RouterLink>
		</div>
	</div>

	<div class="footer" v-if="isShowDialog"></div>
</template>

<style scoped>
.container {
	position: relative;
}

.footer {
	height: 42vw;
}

.container__result {
	font-size: 1.2em;
	text-align: center;
}

.container__dialog-form {
	top: 1vw;
	display: flex;
	justify-content: center;
	position: absolute;
	width: 100%;
}
.container__head h2 {
	text-align: center;
	font-size: 2em;
}

.container__count {
	margin-top: 2vw;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.container__count_good {
	color: #4caf50;
	font-size: 1.5em;
	font-weight: 900;
}

.container__count_fail {
	color: red;
	font-size: 1.5em;
	font-weight: 900;
}

.container__btns {
	margin-top: 2vw;
	display: flex;
	flex-flow: column wrap;
	gap: 2vw;
	align-items: center;
}

.v-enter-active,
.v-leave-active {
	transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
	opacity: 0;
}
</style>
